/*
 * @name: src\login.js
 * @author: EVUN前端组
 * @modify: kelichao
 * @update: 2017-06-13
 * @decript: 登录页逻辑
 * @url: http://git.evun.cn/GMES/GMES_CLIENT_PAD
 */

var initApp = require("initApp");
var util = require("util");
require("particles");

var loginCtrl = {

    init: function() {
        $("#loginFormBox").css("min-height", $(window).height());

        this.initParticles();
        this.changeLanguage();
        this.initForgetUserEvent();
        this.loginSubmit();
        this.event();
    },
    event: function() {
        $(document).on("keydown", function(event) {
            if(event.keyCode === 13) {// 回车键
                $("#loginButton").trigger("click");
            }
        });
    },
    initParticles: function() {
        particlesJS('particles-js',
        
        {
          "particles": {
            "number": {
              "value": 80,
              "density": {
                "enable": true,
                "value_area": 800
              }
            },
            "color": {
              "value": "#ffffff"
            },
            "shape": {
              "type": "circle",
              "stroke": {
                "width": 0,
                "color": "#000000"
              },
              "polygon": {
                "nb_sides": 5
              },
              "image": {
                "src": "img/github.svg",
                "width": 100,
                "height": 100
              }
            },
            "opacity": {
              "value": 0.5,
              "random": false,
              "anim": {
                "enable": false,
                "speed": 1,
                "opacity_min": 0.1,
                "sync": false
              }
            },
            "size": {
              "value": 5,
              "random": true,
              "anim": {
                "enable": false,
                "speed": 40,
                "size_min": 0.1,
                "sync": false
              }
            },
            "line_linked": {
              "enable": true,
              "distance": 150,
              "color": "#ffffff",
              "opacity": 0.4,
              "width": 1
            },
            "move": {
              "enable": true,
              "speed": 6,
              "direction": "none",
              "random": false,
              "straight": false,
              "out_mode": "out",
              "attract": {
                "enable": false,
                "rotateX": 600,
                "rotateY": 1200
              }
            }
          },
          "interactivity": {
            "detect_on": "canvas",
            "events": {
              "onhover": {
                "enable": true,
                "mode": "repulse"
              },
              "onclick": {
                "enable": true,
                "mode": "push"
              },
              "resize": true
            },
            "modes": {
              "grab": {
                "distance": 400,
                "line_linked": {
                  "opacity": 1
                }
              },
              "bubble": {
                "distance": 400,
                "size": 40,
                "duration": 2,
                "opacity": 8,
                "speed": 3
              },
              "repulse": {
                "distance": 200
              },
              "push": {
                "particles_nb": 4
              },
              "remove": {
                "particles_nb": 2
              }
            }
          },
          "retina_detect": true,
          "config_demo": {
            "hide_card": false,
            "background_color": "#b61924",
            "background_image": "",
            "background_position": "50% 50%",
            "background_repeat": "no-repeat",
            "background_size": "cover"
          }
        }
      
      );
    },
    loginSubmit: function() {
        var that = this,
            hasSubmit = false;

        $("#loginButton").on("click", function(e) {

            // 防止重复提交
            if (hasSubmit) {
                return false;
            }

            hasSubmit = true;

            var formData = myApp.formToJSON("#loginForm");

            if ($.trim(formData.username) === "") {
                notie.alert({
                    text: i18next.t("LOGIN.T.MSG_NULl_USERNAME")// 请输入用户名
                });
                
                $("#loginName").focus();
                
                hasSubmit = false;
                return false;
            }

            if ($.trim(formData.password) === "") {
                $("#loginPsw").focus();
                notie.alert({
                    text: i18next.t("LOGIN.T.MSG_NULl_PASSWORD")// 请输入密码
                });
                
                $("#loginPsw").focus();
                
                hasSubmit = false;
                return false;
            }

            security.login(formData.username, formData.password, function() {

                if(!initApp.isInited) {
                    initApp.init();
                }

                Sweet.loadController(Sweet.mainView.activePage.name);
                window.checkLoginStatus = true;
                myApp.closeModal(".login-screen");
                $(document).off("keydown");
                if (typeof that.afterLogin === "function") {
                    that.afterLogin();
                }
                hasSubmit = false;
            });
            hasSubmit = false;
            return false;
        });
    },
    afterLogin: function() {
        Sweet.mainView.router.loadPage("views/index.html");
    },
    show: function() {
        $("#loginFormBox").addClass("on");
        myApp.loginScreen();
    },
    hide: function() {
        $("#loginFormBox").removeClass("on");
        myApp.closeModal(".login-screen");
    },
    changeLanguage: function() {
        util.get("grestful/localeResource/getAllLanguages", {}, null, function(response) {
            var languageButtons = [];

            if (response.data) {
                $.each(response.data, function(index, item) {
                    languageButtons.push({
                        text: item.languageName,
                        onClick: function() {
                            $("#languageCode").val(item.languageCode);
                            console.log("您选择了" + item.languageName + "[" + item.languageCode + "]");
                            i18next.changeLanguage(item.languageCode, function() {});
                        }
                    });
                });
            }

            var actionSheetButtons = [languageButtons, [{
                text: i18next.t("COMMON.B.CANCEL")
            }]];

            $$("#loginChangeLang").on("click", function() {
                myApp.actions(this, actionSheetButtons);
            });
        });
    },
    // 忘记密码操作
    initForgetUserEvent: function() {
        $("#forgetUser").on("click", function() {
            myApp.modal({
                title: i18next.t("LOGIN.T.PASSWORD_RESET"),
                text: i18next.t("LOGIN.T.PASSWORD_RESET_TIP") + 
                        '<div class="login-input"><div sw-role="cell" sw-layout="fit" class="item-input item-input-field">' +
                        '<input type="text" id="resetusername"  placeholder="' + i18next.t('LOGIN.T.USERNAME') + '" ></div>',
                buttons: [{
                    text: i18next.t("COMMON.B.RESET"),
                    onClick: function() {
                        var resetusername = $("#resetusername").val().replace(/(^\s*)|(\s*$)/g, "");
                        if (resetusername === "") {
                            myApp.alert(i18next.t("LOGIN.T.PASSWORD_RESET_TEXTNULl_USERNAME"), i18next.t("APP.COMMON.OPER_ERROR"), function() {
                                $("#resetusername").focus();
                            });
                            return false;
                        } else {
                            myApp.alert(i18next.t("LOGIN.T.PASSWORD_RESET_RESULT_SUCCESS"), i18next.t("APP.COMMON.OPER_SUCCESS"), function() {});
                            return true;
                        }
                    }
                }, {
                    text: i18next.t("COMMON.B.CANCEL")
                }]
            });
        });
    },

};

module.exports = loginCtrl;





